import React from "react";
import s from "./Nav.module.scss";
import { NavLink } from "react-router-dom";

const Nav = () => {
  return (
    <nav className={s.nav}>
      <NavLink className={s.nav__item} to="/profile"></NavLink>
      <NavLink className={s.nav__item} to="/dialogs"></NavLink>
      <NavLink className={s.nav__item} to="/users"></NavLink>
    </nav>
  );
};

export default Nav;
